import type { FunctionComponent, ConstructableComponent, ComponentOptions } from "component-register";
import type { ComponentType } from "solid-element";
import { onMount, createComponent } from "solid-js";

/**
 * Higher-Order Component (HOC) factory to apply styles to a solid-element's renderRoot.
 * @param css The CSS string to apply.
 * @returns A function that takes a ComponentType and returns a new ComponentType with styles applied.
 */

export const styled = <P>(css: string) => {
	return (Component: FunctionComponent<P>): ComponentType<P> => {
		return (props, context) => {
			onMount(() => {
				const styleEl = document.createElement("style");
				styleEl.textContent = css;
				context.element.renderRoot.appendChild(styleEl);
			});
			return Component(props, context);
		};
	};
};

export const Styled = <TProps>(component: ComponentType<TProps>, css: string): ComponentType<TProps> => {
	return (props: TProps, options: ComponentOptions) => {
		onMount(() => {
			const styleEl = document.createElement("style");
			styleEl.textContent = css;
			options.element.renderRoot.appendChild(styleEl);
		});
		if (typeof component === "function") {
			return (component as FunctionComponent<TProps>)(props, options);
		}

		return createComponent(component, props);
	};
};
